@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Noto Sans JP', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

._bdsmTheme_light{
  background-color: rgb(230, 230, 230);
  filter: contrast(1.11);
  transition: all .4s ease-in-out;
}
._bdsmTheme_dark{
  background-color: rgb(21, 25, 29);
  color: white;
  transition: all .4s ease-in-out;
  filter: contrast(0.95);
}

section.bdsm_navsect{
  position:sticky;
  z-index: 2;
  top: 0;
  left: 0;
}

.bdsm_navcomp{
  background-color:#1C8C90;
  color: white;
  display: flex;
  padding: 1rem;
  padding: 1.2em;
  align-items: center;
}
.bdsm_navcomp a.bdsm_header{
  flex: 1;
  color: whitesmoke;
  text-decoration: none;
  font-size: 1.3em;
  font-weight: bold;
}
#bdsm_navbtns{
  display: flex;
  flex-direction: row;
  flex: 1;
}
#bdsm_navbtns a{
  color: whitesmoke;
  text-decoration: none;
  transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
  font-size: 1.2em;
  flex: 1;
  justify-content: center;
  align-items: center;
}
#bdsm_navbtns a:hover{
  text-decoration: underline;
}
@media screen and (max-width: 600px) {
  #bdsm_navbtns{
    flex-direction: column;
  }
}
.bdsm_carousel{
  background-color: #26aaaf;
  object-fit: contain;
  object-position: center;
  background-position-y: 12em;
  background-image: url("https://cdn.discordapp.com/attachments/834126662953336922/850149013708865566/BristolRoad.png");
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: center;
  align-items: center;
  min-height: 20rem;
  font-size: 2em;
  text-overflow:ellipsis;
  padding: .6em;
}
.bdsm_carousel h1{
  background-color: #0b333ace;
  width: 100%;
  height: 100%;
  text-align: center;
  border-radius: 25px;
}
.bdsm_indexBody{
  display: flex;
  text-overflow: clip;
  gap: .4em;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.bdsm_indexBody p{
  font-size: 1.1em;
  display: flex;
  flex-direction: column;
}
.BRMS{
  display: flex;
  flex-direction: column;
}
._iSpan{
  font-size: 1.5em;
  background: rgb(37, 37, 37);
  color: whitesmoke;
  padding: 1em;
  border-radius: 5px;
}
.bdsm_themeSwitcher.dark{
  background-color: whitesmoke;
  border-radius: 50%;
  width: 2.3em;
  height: 2.3em;
  outline: none;
  border: none;
  box-shadow: 5px 5px 1px #dfbd4e inset;
  transition: all .2s ease-in-out;
}
.bdsm_themeSwitcher.light:hover{
  box-shadow: 5px 5px 1px whitesmoke inset;
  background-color: #4ebddf;

}

.bdsm_themeSwitcher.light{
  background-color: whitesmoke;
  border-radius: 50%;
  width: 2.3em;
  height: 2.3em;
  outline: none;
  border: none;
  box-shadow: 5px 5px 1px #4ebddf inset;
  transition: all .2s ease-in-out;
}
.bdsm_themeSwitcher.dark:hover{
  box-shadow: 5px 5px 1px whitesmoke inset;
  background-color: #dfbd4e;

}
.bdsm_app{
  flex: 1;
  height: 100%;
}
.bdsm_themeSwitcher:hover{
  cursor: pointer;
}
.bdsm_eventPage{
  
  min-width: 45%;
  max-width: 65%;
  margin-bottom: 10;
  padding: 5px;
  justify-content: center;
  gap: 10px;
}
.bdsm_eventImages{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  gap: 15px;
}
.bdsm_eventImages img{
  min-width: 5em;
  max-width: 20em;
  object-fit: contain;
  border-radius: 25px;
}
.bdsmTheme_{
  min-height: 100vh;
}